.col {
  padding-bottom: 15px;
  @media screen and (min-width: 1025px) {
    width: 33.3333%;
    &:nth-child(3n + 1) {
      padding-right: 15px;
      padding-left: 0;
    }

    &:nth-child(3n) {
      padding-right: 0;
      padding-left: 15px;
    }
  }

  @media screen and (min-width: 769px) and (max-width: 1024px) {
    width: 50%;
    &:nth-child(1n + 1) {
      padding-right: 15px;
      padding-left: 0;
    }

    &:nth-child(2n) {
      padding-right: 0;
      padding-left: 15px;
    }
  }

  @media screen and (max-width: 768px) {
    width: 100%;
  }
}
