@use "variables";

.scrollup {
  position: fixed;
  right: var(--inset, variables.$scrollup-inset);
  bottom: var(--inset, variables.$scrollup-inset);
  z-index: 50;
  display: inline-block;
  width: var(--size, variables.$scrollup-size);
  height: var(--size, variables.$scrollup-size);
  background-color: variables.$scrollup-background-color;
  cursor: pointer;

  &__icon {
    width: var(--icon-size, variables.$scrollup-icon-size);
    height: var(--icon-size, variables.$scrollup-icon-size);
  }

  &--hidden {
    display: none;
  }
}
