.image {
  display: inline-block;
  width: 100%;
  height: 100%;
  object-fit: contain;

  &--adaptive {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-position: 50% 0;

    @media screen and (max-width: 768px) {
      object-position: center;
    }
  }
}
