@use "variables";

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: variables.$modal-background-color;

  &-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
  }

  &-open {
    display: flex;
  }

  &-content {
    position: absolute;
    top: var(--top, variables.$modal-content-top);
    padding: variables.$modal-padding;
    background: variables.$modal-content-background;
    transform: translateY(-200%);
    transition: variables.$transition;

    &--animate {
      top: 50%;
      transform: translateY(0);
    }
  }
}
