.h-menu {
  position: relative;
  top: calc(50% - 15px);
  display: block;
  min-width: 30px;
  max-height: 30px;
  @apply md:hidden;
  & > span {
    position: relative;
    display: block;
    width: 100%;
    height: 0.1rem;
    margin-bottom: 5px;
    background: #000;
    transition: 0.2s all linear;
  }
}

.h-menu.active {
  & > span {
    &:last-child {
      display: none;
      margin-bottom: 0;
    }
    &:first-child {
      top: 6px;
      transform: rotate(45deg);
    }

    &:nth-child(2) {
      transform: rotate(-45deg);
    }
  }

  & + .menu {
    z-index: 10;
    display: block;
  }
}
