@use "vendor/variables";
@use "sass:color";
@import "vendor/images";
@import "vendor/spinner";
@import "vendor/hamburger";
@import "vendor/scrollup";
@import "vendor/modal";
@import "vendor/grid";

@import "page";

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  min-width: 350px;
  color: theme("textColor.main");
  font-size: variables.$font-size;
  font-family: variables.$font-family;
}

a {
  color: theme("textColor.main");
  cursor: pointer;
  transition: variables.$transition;
  &:hover {
    color: theme("colors.primary");
  }

  &:active {
    color: variables.$link-active;
  }
}

p {
  color: theme("textColor.secondary");
}

h1 {
  font-size: theme("fontSize.4xl");
}

h2 {
  font-size: theme("fontSize.2xl");
}

h3 {
  font-size: theme("fontSize.xl");
}

.icon {
  display: inline-block;
  width: 48px;
  height: 48px;
}

.header,
.footer {
  padding: 2rem 0;
}

.header {
  position: relative;
}

.section {
  &__title {
    display: block;
    padding: 1rem 0;
    text-align: center;
  }
}

.button {
  @extend .button;
  padding: variables.$button-padding;
  &--primary {
    background-color: theme("colors.primary");
    &:hover {
      color: theme("colors.primary");
      background-color: transparent;
    }
  }

  &--secondary {
    &:hover {
      color: #fff;
      background-color: theme("colors.secondary");
    }
  }

  &--bordered {
    border-width: 2px;
    border-style: solid;
    border-color: transparent;

    &:is(.button--primary) {
      &:hover {
        border-color: theme("colors.primary");
      }
    }

    &:is(.button--secondary) {
      border-color: theme("colors.secondary");
    }
  }
}
