@use "vendor/helpers" as *;

.logo {
  position: relative;
  color: inherit;
  line-height: 1;
  &:is(.logo--white) {
    &::before {
      background-image: url("../images/logo-white.svg");
    }
  }
  &:not(.logo--white) {
    &::before {
      background-image: url("../images/logo.svg");
    }
  }
  &::before {
    position: relative;
    display: inline-block;
    width: 64px;
    height: 64px;
    background-repeat: no-repeat;
    background-position: center center;
    content: "";
  }
  span {
    display: block;
    font-weight: bold;
    letter-spacing: 5px;
  }
}

.menu {
  position: absolute;
  z-index: 99;
  width: 100%;
  background-color: theme("colors.primary");
  &__link {
    display: inline-block;
    padding: 10px 20px;
    @apply block md:inline-block;
    &:hover {
      background-color: theme("colors.secondary");
    }
  }
}

.items {
  display: flex;
  flex-wrap: wrap;
  @apply flex-col md:flex-row;
}

.promo {
  color: #fff;
  text-align: left;

  &__title {
    @apply text-4xl uppercase font-bold;
  }

  &__text {
    @apply text-white font-thin;
  }

  @include margins();

  &__row {
    & > * {
      margin-right: 1rem;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 2rem;
  border-width: 1px;
  border-style: solid;
  @include margins();

  &--transparent {
    background-color: transparent;
    border-color: #fff;
    &:hover {
      background-color: #fff;
    }
  }

  &__image {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 175px;
  }

  &-price {
    & > &__out {
      margin-right: theme("margin.4");
    }

    &__out {
      color: theme("colors.gray.400");
    }

    &__main {
      color: theme("colors.red.600");
    }
  }

  &-action {
    position: absolute;
    top: 1rem;
    left: 1rem;
    padding: 0 10px;
    color: #fff;
    text-transform: uppercase;
    background-color: theme("colors.red.600");
  }

  &__text {
    flex: 1 1 auto;
    text-align: center;
  }

  &__link {
    @apply text-main underline underline-offset-4;
  }
}
