@use "sass:color";

$primary: theme("colors.stone.50");
$secondary: theme("colors.gray.700");
$danger: theme("colors.red.600");
$success: theme("colors.green.700");
$warning: theme("colors.orange.700");
$error: theme("colors.red.950");

// FONTS
$font-size: theme("fontSize.base");
$font-color: theme("colors.stone.50");
$font-family: theme("fontFamily.sans");
$text-color: theme("textColor.stone.950");

// LINKS
$link-color: theme("colors.stone.50");
$link-hover: theme("colors.stone.50");
$link-visited: theme("colors.stone.50");
$link-active: theme("colors.stone.50");

// Z-INDEX
$z-sections: theme("zIndex.10");
$z-components: theme("zIndex.20");

// BUTTON
$button-color: inherit;
$button-padding: theme("padding.2") theme("padding.4");
$button-background-color--primary: theme("colors.stone.50");
$button-background-color--secondary: theme("colors.stone.50");
$button-background-color-hover: inherit;

// ANIMATION AND TRANSITIONS
$transition: 0.1s all linear;

// CAROUSEL
$carousel-button-opacity: theme("opacity.10");
$carousel-button-background-color: theme("colors.stone.50");
$carousel-button-top: theme("objectPosition.top");
$carousel-button-right: theme("objectPosition.right");
$carousel-button-left: theme("objectPosition.left");
$carousel-gap: theme("gap.4");

// SCROLLUP
$scrollup-background-color: theme("colors.yellow.400");
$scrollup-color: theme("colors.stone.950");
$scrollup-size: 32px;
$scrollup-icon-size: 100%;
$scrollup-inset: 1rem;

// MODAL
$modal-background-color: rgba(0, 0, 0, 0.4);
$modal-content-background: theme("colors.white");
$modal-content-top: 50%;
$modal-content-size: 320px; // width + height;
$modal-padding: 40px;

// FIGURE CIRCLE
$circle-background-color: #e8eff5;
$circle-size: 100px;
